.skills-page{
    justify-content:"center";
    width: 100%;
    display: grid;
    text-align: center;
    flex-direction: column;
}

.description{
    h5{font-weight: 900;}
}

.skills-container{
    display:flex;
    flex-wrap: wrap;
    grid-gap: 1.016vw;
}

.skills-icons{
    width: 2.1vw;
    aspect-ratio: 1 / 1;
    margin: 0 0.6vw 0;
    filter: invert(var(--contrast));
}

.skill-percent{
    position: absolute;
    top: 0;
    left: 0;
    background-color: var(--red); 
    height: 100%;
    width: 0%;
    transition: width 0.3s cubic-bezier(.68,-0.55,.27,1.55);    
    z-index: -1; 
    color: var(--font-color);
    --s: 50px; /* control the size*/
    --c1: #bd0000;
    --c2: #000000;
    
    --_g:#0000, #0004 5%,
       var(--background) 6%  14%,var(--c1) 16% 24%,var(--background) 26% 34%,var(--c1) 36% 44%,
       var(--background) 46% 54%,var(--c1) 56% 64%,var(--background) 66% 74%,var(--c1) 76% 84%,
       var(--background) 86% 94%,#0004 95%,#0000;
    background:
      radial-gradient(100% 50% at 100% 0   ,var(--_g)),
      radial-gradient(100% 50% at 0    50% ,var(--_g)),
      radial-gradient(100% 50% at 100% 100%,var(--_g));
    background-size: var(--s) calc(2*var(--s));

}

.skills-button-test{
    display: flex;
    text-transform: uppercase;
    border: 2px solid var(--font-color);
    position: relative;
    align-items: center;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    padding: 0.35vw 0.7vw;
    --box-width: 13vw;
    width: var(--box-width);
    height: calc(31 / 100 * var(--box-width));
    overflow: hidden;
    letter-spacing: 1px;
    color: var(--font-color);
    background-color: transparent;
    cursor:pointer;
    transition: 0.3s cubic-bezier(0.42, 0, 1, 1); 

    &:hover{
        transform: translateY(-3px) translateX(3px);
        .skill-percent {width: var(--skills-percentage);}
    }
    .skill-text-test, p{
        display: none;
    }
}

.open{
    height: auto;
    flex-direction: column;
    padding: 20px;
    border-radius: 10px;
    width: 750px;
    height: 200px;
    align-items: start;
    &:hover{
        background-color: rgba(0, 0, 0, 0.05);
    }
    p{display: initial;}
    .skill-text-test{
        display: flex;
        justify-content: center;
        width: 80%;
        text-transform: initial;
    }
    .skills-percentage{
        display: flex;
        width: 100%;
        margin: 0px 0 20px;

    }
    .skill-percent{
        position: relative;
        height: 35px;
        transform: translateX(0);   
        width: var(--skills-percentage);
        white-space: nowrap;
        align-content: center;

    }

    .description{
        width: 100%;
        justify-content: space-between;
        display: flex;
        h5{
            width: 35px;
            align-items: start;
            margin: 0 10px 0;
        }
    }

}

@media (max-width:710px){
    .skills-button-test{
        width: auto;
        height: auto;
        .open{
            width: 100%;
            height: auto;
        }
    }
}

@media (max-width: 255px){
    .skills-icons{
        width: 15vw;
        margin: 0;
}
    .skills-button{
        padding: 0;
        h5{
            font-size: 4.4vw;
        }
    }
}