footer{
display: flex;
flex-direction: column;
height: auto;
width: 100%;
justify-content: end;
align-items: center;
}

.footer-links{
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    text-transform: uppercase;
    a{
        padding: 10px 0;
        text-decoration: none;
        color: var(--font-color);
        display: flex;
        &:hover{
            color: var(--red);
        }
    }
    ul{
    list-style:decimal-leading-zero;
    }
    h4{
        white-space: nowrap;
        align-self: self-end;
    }
}


.footer-title{
    font-size: 18vw;
    text-align: end;
    width: 60%;
    margin: 0;
    padding: 0;
}