header{
    position: fixed;
    top: 0;
    display: flex;
    letter-spacing: 1px;
    flex-direction: column;
    font-size: 13px;
    z-index: 10;
}

.header{
    display: flex;
    width: 100vw;
    padding: 20px 40px 5px 0px;
    justify-content: space-between;
    //transition: 0.6s ease-in-out;
    backdrop-filter: blur(5px);
    


}

.header-titles{
margin: 0;
font-weight: 100;
cursor: pointer;
text-transform: uppercase;
transition: 0.2s ease-in-out;
mix-blend-mode: difference;
    &:hover{
        color: var(--red);
    }
}

.header-titles-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    &:hover{
        flex-direction: column-reverse;
    }
}

.header-button{
    display: flex;
    border: none;
    text-transform: uppercase;
    background-color: transparent;
    margin: 0 10px 0;
    transition: 0.3s ease-in-out;
    cursor: pointer;
    align-items: end;
    color: var(--font-color);
    font-size: 13px;
    &:hover{
        transform: translateY(-3px);
        color: var(--red);
    }
}

.header-button-container{
    display: flex;
    align-content: center;
}

.header-time{
    display: flex;
    justify-content: center;
    margin-bottom: 2px;
    color: var(--red);
    &:hover{
        color: unset;
    }
}

.header-progress-bar {
    position: relative;
    height: 2px;
    background-color: var(--red); 
    margin-right: 20px;
}

@media (max-width:730px){
    .header{
        display: grid;
        flex-direction: column;
        justify-content: center;
        text-align: center;
    }

    .header-button-container{
        justify-content: center;
    }

    .header-time{
       display: none;
    }
}

@media (max-width:330px){
 .header-titles-container{
    h4{
        display: none;
    }
 }
}